import "./index.css";
import { cart, Item } from "./cart";
import * as o from "./option";

const stockItem = (item: Item): string => {
  //discountÎ•º optionÏúºÎ°ú Íµ¨ÌòÑ
  const optionDiscountPrice = o.fromUndefined(item.discountPrice); // {_tag: 'Some', value: 100} or {_tag: 'None'}
  const discountPrice = o.getOrElse(optionDiscountPrice, 0); // 10000 or 0

  let saleText = "";
  if (o.isSome(optionDiscountPrice)) {
    saleText = `(${discountPrice}Ïõê Ìï†Ïù∏)`;
  }

  return `
    <li>
      <h2>${item.name}</h2>
      <div>Í∞ÄÍ≤©: ${item.price - discountPrice}Ïõê ${saleText}</div>
      <div>ÏàòÎüâ: ${item.quantity}ÏÉÅÏûê</div>
    </li>
  `;
};

const outOfStockItem = (item: Item): string => `
  <li class="gray">
    <h2>${item.name} (ÌíàÏ†à)</h2>
    <div class='strike'>Í∞ÄÍ≤©: ${item.price}Ïõê</div>
    <div class='strike'>ÏàòÎüâ: ${item.quantity}ÏÉÅÏûê</div>
  </li>
`;

const item = (item: Item): string => {
  if (item.outOfStock) {
    return outOfStockItem(item);
  } else {
    return stockItem(item);
  }
};

const totalCalculator = (
  list: Array<Item>,
  getValue: (item: Item) => number
) => {
  let result: Array<number> = [];
  list.forEach(function (item) {
    if (item.outOfStock === false) {
      result.push(getValue(item));
    }
  });

  return result.reduce((total, value) => total + value, 0);
};

const totalCount = (list: Array<Item>): string => {
  const totalCount = totalCalculator(list, (item) => item.quantity);
  return `<h2>Ï†ÑÏ≤¥ ÏàòÎüâ: ${totalCount}ÏÉÅÏûê`;
};

const totalPrice = (list: Array<Item>): string => {
  const totalPrice = totalCalculator(
    list,
    (item) => item.price * item.quantity
  );

  const totalDiscountPrice = totalCalculator(list, (item) => {
    // item.discountPrice |> o.fromUndefined($) |> o.getOrElse($, 0)
    const discountPrice = o.getOrElse(o.fromUndefined(item.discountPrice), 0);
    return discountPrice * item.quantity;
  });

  return `<h2>Ï†ÑÏ≤¥ Í∞ÄÍ≤©: ${
    totalPrice - totalDiscountPrice
  }Ïõê (Ï¥ù ${totalDiscountPrice}Ïõê Ìï†Ïù∏)</h2>`;
};

const list = (list: Array<Item>) => {
  return `
    <ul>
    ${list
      // 1. Î™©Î°ùÏóê ÏûàÎäî Î™®Îì† ÏïÑÏù¥ÌÖúÏùÑ ÌÉúÍ∑∏Î°ú Î≥ÄÍ≤Ω
      .map(item)
      // 2. ÌÉúÍ∑∏Ïùò Î™©Î°ùÏùÑ Î™®Îëê ÌïòÎÇòÏùò Î¨∏ÏûêÏó¥Î°ú Ïó∞Í≤∞
      .reduce((tags, tag) => tags + tag, "")}
    </ul>
  `;
};

const app = document.getElementById("app");
if (app != null) {
  app.innerHTML = `
  <h1>Ïû•Î∞îÍµ¨Îãà</h1>
  ${list(cart)}
  ${totalCount(cart)}
  ${totalPrice(cart)}
  `;
}
