// Í∞íÏù¥ ÏûàÏùÑÏàòÎèÑ, ÏóÜÏùÑ ÏàòÎèÑ ÏûàÎäî ÏûêÎ£åÍµ¨Ï°∞

export type Some<A> = {
  readonly _tag: "Some";
  readonly value: A;
};

export type None = {
  readonly _tag: "None";
};

export type Option<A> = Some<A> | None;

//someÍ≥º noneÌÉÄÏûÖÏùò Í∞íÏùÑ ÎßåÎì§Ïñ¥ Ï£ºÎäî Ìï®Ïàò
export const some = <A>(value: A): Option<A> => ({ _tag: "Some", value });

export const none = (): Option<never> => ({ _tag: "None" });

//someÌÉÄÏûÖÏù∏ÏßÄ noneÌÉÄÏûÖÏù∏ÏßÄ ÏïåÏïÑÎ≥¥Îäî Ìï®Ïàò, oa is Some<A>Îäî ÌÉÄÏûÖÍ∞ÄÎìú
export const isSome = <A>(oa: Option<A>): oa is Some<A> => oa._tag === "Some";
export const isNone = <A>(oa: Option<A>): oa is None => oa._tag === "None";

// undefinedÎ•º ÏÇ¨Ïö©Ìï¥ Í∞íÏùò Î∂ÄÏû¨Î•º ÎÇòÌÉÄÎÇ¥Îäî ÌÉÄÏûÖÏùÑ ÏòµÏÖòÏúºÎ°ú Î≥ÄÌôòÌï¥Ï£ºÎäî Ìï®Ïàò

export const fromUndefined = <A>(a: A | undefined): Option<A> => {
  if (a === undefined) return none();
  return some(a);
};

export const getOrElse = <A>(oa: Option<A>, defaultValue: A): A => {
  //Í∞íÏù¥ ÏóÜÏúºÎ©¥ ÏßÄÏ†ïÎêú Í∞íÏùÑ ÏÇ¨Ïö©
  if (isNone(oa)) return defaultValue;
  // Í∞íÏù¥ ÏûàÎã§Î©¥ Ìï¥Îãπ Í∞íÏùÑ ÏÇ¨Ïö©
  return oa.value;
};
